*{margin:0;padding:0;box-sizing:border-box; list-style-type: none;}
html{font-size:10px;}
img{width:100%; max-width: 100%; border:none}
body{font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; height:100vh; display:flex; justify-content: center; align-items: center; background-color: midnightblue;}

.container{width:60rem; padding:3rem; background-color: aquamarine; border-radius:1rem; text-align: center; display:flex; flex-direction: column; gap:3rem;}
h1{font-size:3rem;}
.listContainer{display:flex; flex-direction: column; gap:2rem; align-items: center;}
.listItem{width:80%; border-radius:0.8rem; background-color: rgb(13, 14, 15); padding:1rem 2rem 1rem 0; display:flex; justify-content: sp; gap:1rem; text-align:left; align-items: center; color:rgb(169, 169, 169)}
.userImg{width:8rem; display:flex; justify-content:center; align-items:center;}
.userImg img{border-radius:50%; width:5rem; height:5rem;}
.userProfile{flex:1; display: flex; flex-direction: column;gap:0.5rem}
.userProfile h2{font-size:1.8rem; font-weight:normal}
.userProfile p{font-size:1.4rem; color:darkgray}
.trash{cursor:pointer; color:rgb(216, 75, 75);}
.trash:hover{color:rgb(235, 243, 13);}
.searchBox{width:50%; background-color:azure; border:1px solid grey; padding:1rem; border-radius:1rem; text-align: left;}
.searchBox input{border:none; background-color:transparent;outline:none; font-size:1.6rem}

